import React from 'react';
import { Link } from 'react-router-dom';
import Button from '../../../Button/Button';
import './style.scss';
export default function Total() {
  return (
    <div className="total">
      <h1 className="total__head"> :</h1>
      <div className="total__list">
        <p className="total__list__item">
          <span className="text"> </span>
          <span className="dots"></span>
          <span className="text__dinamic">
             ,
            <br />  42
          </span>
        </p>
        <p className="total__list__item">
          <span className="text"></span> <span className="dots"></span>
          <span className="text__dinamic">Hyndai, i30 N</span>
        </p>
        <p className="total__list__item">
          <span className="text"></span> <span className="dots"></span>
          <span className="text__dinamic"></span>
        </p>
        <p className="total__list__item">
          <span className="text"> </span>
          <span className="dots"></span>
          <span className="text__dinamic">1 2</span>
        </p>
        <p className="total__list__item">
          <span className="text"></span> <span className="dots"></span>
          <span className="text__dinamic"> </span>
        </p>
        <p className="total__list__item">
          <span className="text"> </span>
          <span className="dots"></span>
          <span className="text__dinamic">/</span>
        </p>
        <p className="total__sum">
          <span>:  8000  12000 </span>
        </p>
        <Link to="/need-for-drive/order-page/2">
        <Button type="button big-btn total-btn" title=" "/>
        </Link>
      </div>
    </div>
  );
}
